}

[class*=spacer] {
  clear: both;
  width: 100%;
}

.spacer-xl {
  height: var(--sp-xl);
}

.spacer-lg {
  height: var(--sp-lg);
}

.spacer-md {
  height: var(--sp-md);
}

.spacer-sm {
