}

.py-0 {
  padding-block: 0;
}

.p-sm {
  padding: var(--sp-sm);
}

.px-sm {
  padding-inline: var(--sp-sm);
}

.py-sm {
  padding-block: var(--sp-sm);
}

.p-md {
  padding: var(--sp-md);
}

.px-md {
  padding-inline: var(--sp-md);
}

.py-md {
  padding-block: var(--sp-md);
}

.p-lg {
  padding: var(--sp-lg);
}

.px-lg {
  padding-inline: var(--sp-lg);
}

.py-lg {
  padding-block: var(--sp-lg);
}

@media (min-width: 1200px) {
  .pl-xl-xl {
    padding-left: var(--sp-xl);
  }
}
.image {
  position: relative;
}
.image img {
  display: block;
  position: relative;
  width: 100%;
}

:where(.image-cover),
:where(.video-cover),
:where(.image-contain) {
  position: relative;
  overflow: hidden;
  padding-bottom: 50%;
}

:where(.image-round) {
  overflow: hidden;
  border-radius: 100%;
  padding-bottom: 100%;
}

.video-cover video,
.image-cover img,
.image-contain img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  content-visibility: auto;
}

.image-contain img {
  object-fit: scale-down;
}

a .image-cover img,
a .video-cover video {
  transition: transform 1s;
  transform: scale(1.0001);
}

@media (min-width: 1200px) {
  a:hover .image-cover img,
  a:hover .video-cover video {
    transform: scale(1.05);
  }
}
.g-xl,
.gy-xl {
