/*--------------------*/
/*  Typography        */
/*--------------------*/
body {
  font-family: var(--ff-1);
  font-weight: 300;
  font-size: var(--text);
  line-height: 1.5;
  background-color: #f5f1ed;
  color: var(--clr-text);
}

.text {
  --text-space: 1rem;
  --text-rd: var(--rd-1);
  --text-li-padding: 2rem;
  --text-th-height: 4rem;
  --text-th-padding: 1.5rem;
  font-size: var(--text);
  line-height: 1.5;
  font-weight: 300;
  color: var(--clr-text);
}
.text * + :where(*) {
  margin-top: calc(var(--text-space) * 3);
}
.text * + :is(p, ul, ol, li) {
  margin-top: var(--text-space);
}
.text :is(img, table, iframe, figure, picture, blockquote, .swiper-entry) + *:not(figcaption) {
  margin-top: calc(var(--text-space) * 3);
}
.text .swiper-entry > * * {
  margin-top: 0;
}
.text_sm {
  font-size: var(--text-sm);
  line-height: 1.429;
}
.text_lg {
  font-size: var(--text-lg);
  line-height: 1.55;
}
.text_xl {
  font-size: var(--h6);
  line-height: 1.3;
}

.text.type-2 {
  --text-space: 0.5rem;
}
.text.type-2 ul {
  --imgSz: 0.875rem;
}
.text.type-2 ul li {
  text-transform: uppercase;
  font-family: var(--ff-2);
  color: var(--clr-title);
  font-weight: 500;
}
.text.type-2 ul li:before {
  content: "";
  width: var(--imgSz);
  height: var(--imgSz);
  top: 0.3em;
  background: url("../img/icons/link-decor.svg");
  background-size: contain;
  background-position: center;
}
.text.type-2 p {
  line-height: 1.1;
}
.text.type-2 * + :where(*) {
  margin-top: var(--text-space);
}
.text.type-2 * + :where(.text_xl) {
  margin-top: calc(var(--text-space) * 3);
}

.text .label {
  position: relative;
  display: inline-flex;
  align-items: center;
  --imgSz: 1.25rem;
  --pd: 0.75rem;
  padding: calc(var(--pd) / 1.5) var(--pd);
  padding-left: calc(var(--imgSz) + var(--pd) + 0.625rem);
  background-color: var(--clr-grey);
  border-radius: var(--rd-2);
  line-height: 1.5;
}
.text .label:before {
  position: absolute;
  left: var(--pd);
  content: "";
  width: var(--imgSz);
  height: var(--imgSz);
  top: 50%;
  transform: translateY(-50%);
  background: url("../img/icons/icon-info.svg");
  background-size: contain;
  background-position: center;
}

.text b {
  font-weight: 400;
}

.text b,
.text span {
  color: var(--clr-title);
}

.text strong {
  font-weight: 500;
}

.text em {
  font-style: italic;
  font-weight: 500;
}

.text a:not(.btn) {
  display: inline;
  font-weight: 300;
  color: var(--clr-primary);
  text-decoration: underline;
  text-decoration-color: transparent;
}

.text ul li,
.text ol li {
  position: relative;
  padding-left: var(--text-li-padding);
}

.text ul li:before {
  content: "";
  position: absolute;
  left: calc(var(--text-li-padding) / 2 - 0.5em);
  top: 0.5em;
  width: 0.5em;
  height: 0.5em;
  border-radius: 100%;
  background-color: var(--clr-primary);
}

.text ol {
  counter-reset: number;
}
.text ol li:before {
  counter-increment: number;
  content: counter(number, decimal-leading-zero) ".";
  display: inline-block;
  position: absolute;
  left: 0;
  font-weight: 500;
  line-height: inherit;
  color: var(--clr-primary);
}

.text img {
  display: block;
  width: 100%;
  border-radius: var(--text-rd);
}

.text figure {
  line-height: 0;
  width: 100%;
}
.text figure.aligncenter {
  text-align: center;
}
.text figure.alignright {
  text-align: right;
}
.text figure > img {
  width: auto;
}
