    letter-spacing: 1px;
  }
}

h2,
.h2 {
  font-size: var(--h2);
  line-height: 1.214;
}

.h2-lg {
  font-size: var(--h2-lg);
  line-height: 1.214;
}

h3,
.h3 {
  font-size: var(--h3);
  line-height: 1.25;
}

h4,
.h4 {
  font-size: var(--h4);
  line-height: 1.14;
}

h5,
.h5 {
  font-size: var(--h5);
  line-height: 1.4em;
}

h6,
.h6 {
