}

.title-margin-2 {
  margin-bottom: 2rem;
}

.text-balance {
  text-wrap: balance;
}

.text-pretty {
  text-wrap: pretty;
}

.text-left {
  text-align: left;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

@media (min-width: 1200px) {
  .text a:not(.btn):hover {
    text-decoration-color: currentColor;
  }
}
@media (max-width: 991px) {
  .text {
    --text-space: 0.75rem;
    --text-li-padding: 1.5rem;
    --text-th-height: 3.5rem;
    --text-th-padding: 0.5rem;
  }
}
@keyframes move-out {
  from {
    transform: translateY(0%);
  }
  to {
    transform: translateY(-100%);
  }
}
@keyframes move-in {
  from {
    transform: translateY(100%);
  }
  to {
    transform: translateY(0%);
  }
}
text-animate {
  opacity: 0;
}

.text-animated {
  opacity: 1;
}

.text-animate-word {
  position: relative;
  overflow: hidden;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  line-height: inherit;
}

.text-animate-word.animated > * {
  -webkit-transform: translateY(0);
  transform: translateY(0);
  opacity: 1;
}

.text-animate-word > * {
  display: inline-block;
  -webkit-transform: translateY(100%);
  transform: translateY(100%);
  opacity: 0;
  -webkit-transition: 1s;
  transition: 1s;
  -webkit-transform-origin: left top;
  transform-origin: left top;
}

.text-animate__word > span {
  display: inline-block;
  -webkit-transform: translateY(100%);
  transform: translateY(100%);
  opacity: 0;
  -webkit-transition: 1s;
  transition: 1s;
  -webkit-transform-origin: left top;
  transform-origin: left top;
}

.text-animate__word.animated > span {
  -webkit-transform: translateY(0);
  transform: translateY(0);
  opacity: 1;
}

.rellax-img {
  height: 120% !important;
  top: -5% !important;
}

@media (max-width: 991px) {
  .rellax-img {
    height: 140% !important;
    top: -10% !important;
  }
}
.slideUp {
  transform: translateY(50px);
  opacity: 0;
  transition: opacity var(--transition-2), transform var(--transition-2);
}

.slideLeft {
  transform: translateX(100px);
  opacity: 0;
  transition: opacity var(--cubic-1), transform var(--cubic-1);
}

.slideLeft-2 {
  transform: translateX(300px);
  opacity: 0;
  transition: opacity var(--transition-2), transform var(--transition-2);
}

.slideRight {
  transform: translateX(-100px);
  opacity: 0;
  transition: opacity var(--transition-2), transform var(--transition-2);
}

.slideRight-2 {
  transform: translateX(-300px);
  opacity: 0;
  transition: opacity var(--transition-2), transform var(--transition-2);
}

.fadeIn {
  opacity: 0;
  transition: opacity var(--transition-2);
}

@keyframes moveSlideshow {
  100% {
    transform: translateX(100%);
  }
}
.animated.slideRight,
.animated.slideLeft,
.animated.slideLeft-2,
.animated.slideRight-2,
.animated.slideUp,
.animated.fadeIn {
  opacity: 1;
  transform: none;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
/*--------------------*/
/*  Accordion         */
/*--------------------*/
.accordion {
  position: relative;
  z-index: 1;
}

.accordion-item {
  --padding-acc: 1.625rem;
  --iSz: 2.625rem;
  --i-before-w: 1rem;
  --clr-border: #ead7cc;
  position: relative;
  border-bottom: 0.0625rem solid var(--clr-border);
}
.accordion-item:first-child {
  border-top: 0.0625rem solid var(--clr-border);
}

.accordion-title {
  position: relative;
  font-family: var(--ff-2);
  cursor: pointer;
  font-size: var(--h6);
  line-height: 1.36em;
  font-weight: 500;
  color: var(--clr-title);
  padding-block: var(--padding-acc);
  padding-left: calc(var(--iSz) + var(--padding-acc));
  transition: var(--transition-1);
}
.accordion-title i {
  display: inline-block;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  border: 1px solid var(--clr-border);
  border-radius: 50%;
  width: var(--iSz);
  height: var(--iSz);
  transition: var(--transition-1);
}
.accordion-title i::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: var(--i-before-w);
  height: 0.5px;
  background-color: var(--clr-primary);
  transition: var(--transition-1);
  transform: translate(-50%, -50%);
}
.accordion-title i::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0.5px;
  height: var(--i-before-w);
  background-color: var(--clr-primary);
  transform: translate(-50%, -50%);
  transition: var(--transition-1);
}
.accordion-title.is-active i::after {
  transform: translate(-50%, -50%) scaleY(0);
}

.accordion-inner {
  display: none;
  padding-bottom: var(--padding-acc);
  padding-left: calc(var(--iSz) + var(--padding-acc));
}
.accordion-inner > .text {
  font-size: var(--text-sm);
  width: min(21.25rem, 100%);
}

@media (min-width: 1200px) {
  .accordion-title:hover {
    color: var(--clr-primary);
  }
}
@media (max-width: 991px) {
  .accordion-item {
    --padding-acc: 1.25rem;
    --i-before-w: 0.75rem;
  }
}
/*--------------------*/
/*  Buttons           */
/*--------------------*/
:root {
  --btnSz: 2.65rem;
  --btnSzSm: 2rem;
}

.btn {
  min-height: var(--btnSz);
  z-index: 1;
  position: relative;
  user-select: none;
  cursor: pointer;
  overflow: hidden;
  font-size: 1rem;
  line-height: 1.5;
  font-weight: 300;
  color: var(--clr-white);
  text-decoration: none;
  padding: 0.5625rem 1.25rem;
  border: none;
  text-transform: uppercase;
  border-radius: var(--rd-1);
  transition: var(--transition-1);
  flex-shrink: 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
.btn b {
  display: inline-block;
  line-height: inherit;
  font-weight: inherit;
  color: currentColor;
}
.btn > * + * {
  margin-left: 0.8em;
}
.btn-primary {
  background-color: var(--clr-primary);
  color: var(--clr-btn);
}
.btn svg {
  color: currentColor;
}
.btn svg *[stroke] {
  stroke: currentColor;
}
.btn svg *[fill] {
  fill: currentColor;
}
.btn-block {
  width: 100%;
}
.btn.disabled {
  pointer-events: none;
  background-color: var(--clr-grey);
}
.btn:has(svg:only-child) {
  border-radius: 100%;
}
.btn:has(svg:only-child) svg {
  width: 1.2em;
  height: 1.2em;
}
.btn-sm {
  min-height: var(--btnSzSm);
  font-size: 0.875rem;
  line-height: 1.42;
  padding: 0.375rem 1.0625rem;
}

.btn-group {
  min-height: var(--btnSz);
  --space: 0.5rem;
  --pd: 1.25rem;
  z-index: 1;
  position: relative;
  user-select: none;
  cursor: pointer;
  overflow: hidden;
  font-size: 1rem;
  line-height: 1.5;
  font-weight: 300;
  color: var(--clr-btn);
  text-decoration: none;
  border: none;
  text-transform: uppercase;
  transition: var(--transition-2);
  flex-shrink: 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: var(--space);
}
.btn-group b {
  border-radius: var(--rd-1);
  padding: calc(var(--pd) / 2 - 1px) var(--pd);
  background-color: var(--clr-primary);
  height: 100%;
  transition: var(--transition-2);
}

.btn-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  --size: 2.625rem;
  line-height: 1;
  z-index: 1;
  flex: 0 0 var(--size);
  width: var(--size);
  height: var(--size);
  border-radius: 100%;
  background: transparent;
  color: var(--clr-black);
  border: 0.0625rem solid var(--clr-border);
  cursor: pointer;
  transition: var(--transition-2) !important;
}
.btn-icon svg {
  --size: 1rem;
  width: var(--size);
  height: var(--size);
  color: currentColor;
}
.btn-icon svg *[stroke] {
  stroke: currentColor;
}
.btn-icon svg *[fill] {
  fill: currentColor;
}
.btn-icon svg > * {
  display: block;
  width: 100%;
  height: 100%;
}
.btn-icon.type-2 {
  color: var(--clr-primary);
}
.btn-icon.type-2 svg {
  --size: 1.5rem;
}

.btn-close {
  display: inline-block;
  position: relative;
  cursor: pointer;
  --bw: 2.75rem;
  width: var(--bw);
  height: var(--bw);
  color: var(--clr-primary-400);
  border-radius: 100%;
  background-color: transparent;
  border: 0.0625rem solid var(--clr-primary);
  transition: var(--transition-1);
}
.btn-close::before, .btn-close::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 50%;
  height: 0.125rem;
  transform: translate(-50%, -50%) rotate(45deg);
  background-color: currentColor;
}
.btn-close::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.btn-close.type-2 {
  --bw: unset;
  border-radius: var(--rd-1);
  padding: 0.375rem 1.375rem;
}
.btn-close.type-2 span {
  color: var(--clr-primary-400);
  font-size: var(--text-sm);
  line-height: 1.42;
  text-transform: uppercase;
  transition: var(--transition-1);
}
.btn-close.type-2::before, .btn-close.type-2::after {
  display: none;
}

.btn-link {
  display: inline-block;
  cursor: pointer;
  font-size: 1rem;
  line-height: 1.38em;
  color: var(--clr-title);
  font-weight: 300;
  text-decoration: none;
  transition: var(--transition-1);
}

.btn-wrap {
  display: inline-flex;
  align-items: center;
  gap: 0.25rem;
}

@media (min-width: 1200px) {
  .btn:hover {
    background-color: #c08667;
  }
  .btn-primary:hover {
    color: var(--clr-btn);
    background-color: #c08667;
  }
  .btn-circle:hover {
    color: var(--clr-primary);
    border-color: var(--clr-primary);
  }
  .btn-close:hover {
    color: var(--clr-primary);
    background-color: var(--clr-grey);
  }
  .btn-close.type-2:hover {
    background-color: var(--clr-primary);
  }
  .btn-close.type-2:hover span {
    color: var(--clr-white);
  }
  .btn-link:hover {
    color: var(--clr-primary);
    text-decoration: underline;
    text-decoration-color: currentColor;
  }
  .btn-group:not(.type-2):hover .btn-icon {
    background-color: var(--clr-primary);
    color: var(--clr-white);
    border-color: transparent;
    transform: translateX(calc(var(--space) - 1rem + var(--pd) * -1));
    border-radius: 0 50% 50% 0;
  }
  .btn-group.type-2:hover .btn-icon {
    background-color: var(--clr-primary);
    color: var(--clr-white);
    border-color: transparent;
    border-radius: 0 50% 50% 0;
  }
  .btn-group.type-2:hover b {
    border-radius: var(--rd-1) 0 0 var(--rd-1);
    transform: translateX(calc((var(--space)) + var(--pd)));
  }
  .btn-icon:hover {
    color: var(--clr-primary);
    border-color: var(--clr-primary);
  }
}
@media (max-width: 1199px) {
  .btn-link {
    font-size: 1.125rem;
  }
}
@media (max-width: 991px) {
  .btn-link {
    font-size: 1rem;
  }
  .btn-close {
    --bw: 2rem;
  }
}
/*--------------------*/
