:root {
  --pg-space: max(1rem, 2.5vw);
  --bullet-space: 0.25rem;
  --sw-btn: 2.625rem;
  --sw-bullet: 0.375rem;
}

:host {
  position: relative;
  z-index: 1;
  display: block;
  margin-inline: auto;
}

.swiper-entry {
  position: relative;
}

.swiper-container {
  overflow: hidden;
  position: relative;
  z-index: 1;
  padding: 0;
  display: block;
  list-style: none;
  margin-inline: auto;
}

.swiper-horizontal {
  touch-action: pan-y;
}

.swiper-vertical {
  touch-action: pan-x;
}
.swiper-vertical > .swiper-wrapper {
  flex-direction: column;
  cursor: url(../img/drag-vertical.png) 8 16, ns-resize;
}

.swiper-wrapper {
  box-sizing: border-box;
  position: relative;
  z-index: 1;
  width: 100%;
  height: 100%;
  display: flex;
  transition-property: transform;
  transition-timing-function: var(--transition-timing, initial);
  cursor: url(../img/drag.png) 16 9, ew-resize;
}

@media (max-width: 575px) {
  .swiper-container {
    overflow: visible;
  }
}
.swiper-slide {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  flex-shrink: 0;
  transition-property: transform;
}
.swiper-slide-invisible-blank {
  visibility: hidden;
}

.swiper-android .swiper-slide,
.swiper-ios .swiper-slide,
.swiper-wrapper {
  transform: translate3d(0, 0, 0);
}

[class*=swiper-button] {
  cursor: pointer;
  position: absolute;
  overflow: hidden;
  z-index: 1;
  color: var(--clr-title);
  right: auto;
  top: calc(50% - var(--sw-btn) / 2);
  left: calc(-1 * var(--sw-btn) - 1rem);
  margin: 0;
  flex: 0 0 var(--sw-btn);
  width: var(--sw-btn);
  height: var(--sw-btn);
  border: 0.0625rem solid currentColor;
  border-radius: var(--rd-1);
  transition: var(--transition-1);
}
[class*=swiper-button]::before {
  content: "";
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 70%;
  height: 70%;
  background: url("../img/icons/icon-arrow.svg") no-repeat center/contain;
  filter: invert(1);
  transform: translate(-50%, -50%) rotate(180deg);
}
[class*=swiper-button]::after {
  content: "";
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 0%;
  background-image: linear-gradient(90deg, #ab5e34 0%, #ab5e34 100%);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center bottom;
  transition: var(--transition-1);
}

.swiper-button-next {
  left: auto;
  right: calc(-1 * var(--sw-btn) - 1rem);
  transform: rotate(180deg);
}

.swiper-button-disabled {
  opacity: 0.35;
  cursor: auto;
  pointer-events: none;
}

.swiper-button-hidden {
  opacity: 0;
  cursor: auto;
  pointer-events: none;
}

.swiper-controls-wrap {
  position: relative;
  line-height: 0;
  display: flex;
  margin-inline: auto;
  align-items: center;
  gap: 2.5rem;
}
.swiper-controls-wrap .swiper-button-prev,
.swiper-controls-wrap .swiper-button-next {
  position: relative;
  top: 0;
  left: 0;
  right: 0;
}

.section:has(.swiper-button-lock) .swiper-wrapper {
  cursor: default !important;
}
.section:has(.swiper-button-lock) .swiper-controls-wrap {
  display: none !important;
}

.swiper-button-lock {
  display: none !important;
}

@media (min-width: 1200px) {
  [class*=swiper-button]:hover {
    color: var(--clr-primary);
    border-color: var(--clr-primary);
  }
  [class*=swiper-button]:hover::after {
    height: 100%;
  }
}
@media (max-width: 1199px) {
  [class*=swiper-button] {
    display: none;
  }
}
/* Global */
.swiper-pagination {
  z-index: 10;
  transition: 300ms opacity;
  transform: translate3d(0, 0, 0);
  line-height: 0;
  width: 100%;
  text-align: center;
}
.swiper-pagination:not(.swiper-pagination-relative) {
  position: absolute;
  bottom: var(--pg-space);
  left: 0;
}
.swiper-pagination.swiper-pagination-relative {
  position: relative;
  margin-top: var(--pg-space);
}
.swiper-pagination.swiper-pagination-hidden {
  opacity: 0;
}
.swiper-pagination-disabled > .swiper-pagination, .swiper-pagination.swiper-pagination-disabled {
  display: none !important;
}

/* Bullets */
.swiper-pagination-bullet {
  position: relative;
  display: inline-block;
  vertical-align: top;
  width: var(--sw-bullet);
  height: var(--sw-bullet);
  opacity: 1;
  border-radius: 100%;
  margin: 0 var(--bullet-space) !important;
  background: #d5ae99;
  opacity: 0.2;
  transition: var(--transition-1);
}
.swiper-pagination-bullet-active {
  opacity: 1;
  background: #d5ae99;
}
button.swiper-pagination-bullet {
  border: none;
  margin: 0;
  padding: 0;
  box-shadow: none;
  appearance: none;
}

.swiper-pagination-clickable .swiper-pagination-bullet {
  cursor: pointer;
}
.swiper-pagination-bullet:only-child {
  display: none !important;
}

/* Bullets Dynamic */
.swiper-pagination-bullets-dynamic {
  overflow: hidden;
  font-size: 0;
  left: 50%;
  transform: translateX(-50%);
  white-space: nowrap;
}
.swiper-pagination-bullets-dynamic .swiper-pagination-bullet {
  position: relative;
  transform: scale(0.33);
  transition: 200ms transform, 200ms left;
}
.swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active {
  transform: scale(1);
}
.swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active-main {
  transform: scale(1);
}
.swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active-prev {
  transform: scale(0.66);
}
.swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active-prev-prev {
  transform: scale(0.33);
}
.swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active-next {
  transform: scale(0.66);
}
.swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active-next-next {
  transform: scale(0.33);
}

.swiper-horizontal.swiper-rtl > .swiper-pagination-bullets-dynamic .swiper-pagination-bullet {
  transition: 200ms transform, 200ms right;
}

/* Fraction */
.swiper-pagination-fraction {
  font-size: var(--text);
  line-height: 1.4em;
  font-weight: 500;
  color: var(--clr-title);
}

/* For Vertical Slider */
.swiper-vertical > .swiper-pagination-bullets,
.swiper-pagination-vertical.swiper-pagination-bullets {
  top: 50%;
  left: auto;
  right: 0.5rem;
  transform: translate3d(0, -50%, 0);
}
.swiper-vertical > .swiper-pagination-bullets .swiper-pagination-bullet,
.swiper-pagination-vertical.swiper-pagination-bullets .swiper-pagination-bullet {
  margin: var(--bullet-space) 0 !important;
  display: block;
}
.swiper-vertical > .swiper-pagination-bullets.swiper-pagination-bullets-dynamic,
.swiper-pagination-vertical.swiper-pagination-bullets.swiper-pagination-bullets-dynamic {
  top: 50%;
  transform: translateY(-50%);
  width: 0.5rem;
}
.swiper-vertical > .swiper-pagination-bullets.swiper-pagination-bullets-dynamic .swiper-pagination-bullet,
.swiper-pagination-vertical.swiper-pagination-bullets.swiper-pagination-bullets-dynamic .swiper-pagination-bullet {
  display: inline-block;
  transition: 200ms transform, 200ms top;
}

/* For Lock */
.section:has(.swiper-pagination-lock) .swiper-wrapper {
  cursor: default !important;
}
.section:has(.swiper-pagination-lock) .swiper-controls-wrap {
  display: none !important;
}

.swiper-pagination-lock {
  display: none !important;
}

.swiper-3d, .swiper-3d.swiper-css-mode .swiper-wrapper {
  perspective: 1200px;
}
.swiper-3d .swiper-wrapper,
.swiper-3d .swiper-cube-shadow,
.swiper-3d .swiper-slide {
  transform-style: preserve-3d;
}
.swiper-3d [class*=swiper-slide-shadow] {
  pointer-events: none;
  position: absolute;
  z-index: 10;
  inset: 0;
}
.swiper-3d .swiper-slide-shadow {
  background: rgba(0, 0, 0, 0.15);
}
.swiper-3d .swiper-slide-shadow-left {
  background-image: linear-gradient(to left, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}
.swiper-3d .swiper-slide-shadow-right {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}
.swiper-3d .swiper-slide-shadow-top {
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}
.swiper-3d .swiper-slide-shadow-bottom {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}